import React, { Component,Fragment } from 'react'
import { RECOMMENDLINE } from '@/redux/recommend/variable.js'
import {connect} from 'react-redux'//引入redux容器组件
import { selectSiteDetails , selectLineCars } from '@/tool/api/real_bus.js'
import { _mergeList,_getDistance,_setLocation,deepCopy,_filterListView} from '@/tool/Processing.js'
import BScroll from 'better-scroll'
import Busheader from '@/components/busHeader/index'
import map6 from '@/assets/imgs/map6.png'
import banner from '@/assets/imgs/banner.png'
import arrowsFFF from '@/assets/imgs/arrowsFFF.png'
import xin from '@/assets/imgs/xin.png'
import line_bus from '@/assets/imgs/line_bus.png'
import mapMy from '@/assets/imgs/mapMy.png'
import mero from '@/assets/imgs/mero.png'
import signal from '@/assets/imgs/signal.gif'
import ref from '@/assets/imgs/ref.png'
import bur from '@/assets/imgs/bur.png'
import downRight from '@/assets/imgs/downRight.png'
import './index.scss'
const backgroundBanner={
    backgroundSize:'100% 100%',
    backgroundRepeat:'no-repeat',
    backgroundPosition:'center',
    backgroundOrigin:'content-box',
    backgroundImage: "url(" + banner + ")"
}
const flexCenter={
    justifyContent:'center'
}
const flexEnd={
    justifyContent:'flex-end'
}
const widthCenter={
    width:'28px',
    height:'20px'
}
const widthEnd={
    width:'34px',
    height:'26px'
}
const DETAILSFIXED=[
    {id:1,text:'转向',img:bur},
    {id:2,text:'刷新',img:ref},
]
// siteOrder  根据自身定位更新数据   明天做
class Details extends Component {
    state={
        upDown:1,//车辆方向 
        lineDetails:{},
        siteOrder:null,
        viewData:{viewList:[],list:[]},
        selfLocation:null,//自身定位的图标控制属性
        mapView:null,//控制ifame的显示和隐藏
        clictHeight:null,//页面可视区域高度
        arriveTIME:null,
        timer:null
    }
    componentDidMount(){//DOM挂载完成后初始化页面数据  添加better-scroll 获取页面可视高度
        this._getSiteDetails()
        this.state.timer=setInterval(()=>{
            this._getLineTime()
        },15000)
        let clictHeight=document.documentElement.clientHeight || document.body.clientHeight;
        let arriveTIME=JSON.parse(sessionStorage.getItem('busId'))
        setTimeout(()=>{
            const wrapper = document.querySelector('.wrapper');
            this.scroll = new BScroll(wrapper, {
                scrollX: true,  //开启横向滚动
                click: true,  //开启点击
              })
        },1000)
        this.setState({clictHeight,arriveTIME})
    }
    componentWillUnmount(){
        clearInterval(this.state.timer&&this.state.timer)
        sessionStorage.removeItem('busId')
    }
    _getSiteDetails =  async () =>{//车辆位置信息
        const { lineId,siteOrder:siteOrderOK,upDown:upDownOK }=this.props.location.state
        // console.log(siteOrderOK,'siteOrderOK');
        const { latitude,longitude,userId }=this.props.mylocation
        let { upDown,siteOrder } =this.state
        let seach={
            userId:userId,
            lineId:lineId,
            siteId:lineId,
            upDown:upDown,
            longitude:longitude,
            latitude:latitude
        }
        let {code,data:siteList}=await selectSiteDetails(seach)
        if( code!==200) return
        siteList.siteDtos.forEach(item=>{
            item.distance=_getDistance(latitude,longitude,item.latitude,item.longitude)//算距离
            item.busLocat={}
        })
        if(!siteOrder){
            let order=deepCopy(siteList.siteDtos).sort((a,b)=>a.distance-b.distance)[0].viewSort
            siteOrder=siteOrderOK?siteOrderOK:order
        }
        this.setState({
            lineDetails:siteList,
            siteOrder:siteOrder
        },()=>{this._getLineTime()})
    }
    _getLineTime = async ()=>{//实时车辆数据
        const { lineId }=this.props.location.state
        const { userId }=this.props.mylocation
        let {siteOrder,upDown,lineDetails,selfLocation}=this.state
        let { siteDtos }=lineDetails
        let seachNew= [{
            carNum: 0,
            lineId: lineId,
            siteOrder:siteOrder, 
            siteRoad: 1, 
            upDown:upDown }]
        let {code , data:busList} = await selectLineCars({
            userid: userId,
            lineSiteCarsParams: seachNew
          })
        if( code!==200) return
        let { siteRoads,list } =busList[0]
        let newObj={color: "#1BAC2E", num: (siteRoads.length)+1, roadCondition: "畅通"}
        let viewList=_mergeList([...siteRoads,newObj],siteDtos)
        //初始化距离用户当前最近位置开始
        if(!selfLocation){
            selfLocation=deepCopy(viewList).sort((a,b)=>a.distance-b.distance)[0].viewSort
        }
        //初始化距离用户当前最近位置结束
        // 合并车辆信息开始
        viewList= _setLocation(viewList,list)
        // 合并车辆信息结束
        busList[0].viewList=viewList
        this.setState({viewData:busList[0],selfLocation})
    }
    _setWidth = (w) =>{//配合better-scroll的宽度
        return w*74+'px'
    }
    _setStyle = (color) =>{//线路样式
        return {
            backgroundColor:color,
            width: '74px',
            flexShrink:0
        }
    }
    _setBackground = (runStauts) =>{//设置banner背景图
        let viewBackground=runStauts&&runStauts===2?
        ({backgroundColor:'#ffffff'})
        :backgroundBanner
        return viewBackground
    }
    _setBannerStyle = (runStauts,list) =>{//设置banner样式
        let listView=_filterListView(list).length
        let bannerStyle=runStauts&&runStauts===2&&listView>1
        ?({justifyContent:'space-between'})
        :({justifyContent:'center'})
        return bannerStyle
    }
    _setBusImgDOM = ( busLocat ) =>{//渲染车辆图形DOM
        let is_null = Object.keys( busLocat ).length
        if(is_null){
            let { pullIn }= busLocat
            return (
                <div className="line_image" style={pullIn===3?flexEnd:flexCenter}>
                    <img style={pullIn===3?widthEnd:widthCenter} src={line_bus} alt="" />
                </div>
            )
        }else{
            return null
        }
    }
    _setChildredDOM = (list) =>{//线路名称
        let { viewList } = this.state.viewData
        let viewL=deepCopy(viewList).sort((a,b)=>a.distance-b.distance)[0]//拷贝数据后排序
        let viewChildred=viewL.viewSort===list.viewSort
        ?(
            <div className="line_name colo" onClick={()=>this._setLocationSelf(list.viewSort)}>
                <span>{list.viewSort}</span>
                <span>{list.name}</span>
                <img src={xin} alt="" />
            </div>
        )
        :(
            <div className="line_name" onClick={()=>this._setLocationSelf(list.viewSort)}>
                <span>{list.viewSort}</span>
                <span>{list.name}</span>
            </div>
        )
        return viewChildred
    }
    _setSelfLocation = (list) =>{//自身定位 
        const { selfLocation } =this.state
        let viewSelf=selfLocation===list.viewSort
        ?(<div className="line_self">
            <img src={mapMy} alt="" />
        </div>)
        :null
        return viewSelf
    }
    _setIfameDOM = () =>{
        const { mapView,clictHeight } = this.state
        let viewIfame=mapView
        ?(
            <iframe
                title="resg"
                src={mapView}
                style={{ width: '100%', border: '0px', height:clictHeight+'px'}}
                sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
                scrolling="auto"
            />
        )
        :null
        return viewIfame
    }
    _getIfame = (address) =>{//ifame
        this.props.history.push({pathname:'/map',state:{address}})
    }
    _setLocationSelf = (value) =>{//根据点击位置请求位置数据
        this.setState({
            siteOrder:value,//请求参数
            selfLocation:value//位置
        },()=>{
            this._getLineTime()
        })
    }
    _goBack = () =>{//返回上一页
        const { mapView } = this.state
        if(!mapView){
            this.props.history.goBack()
        }else{
            this.setState({
                mapView:null
            })
        }
    }
    _setBusStateDOM = (runStauts,list) =>{
        const { siteOrder,viewData,lineDetails } = this.state
        const { latitude,longitude }=this.props.mylocation
        const { viewList } = viewData
        if(!viewList.length) return null
        let listView=_filterListView(list)
        let VIEWDOM
        let DOMNULL=(<div className="main_notyet">
                <div className="notyet_text">赞未发班</div>
                <div className="notyet_font">{siteOrder}站 /{_getDistance(latitude,longitude,viewList[0].latitude,viewList[0].longitude)/1000}km</div>
            </div>)
        if(runStauts===1) VIEWDOM=DOMNULL
        if(runStauts===2)  VIEWDOM=listView.length?this._setChildrenBusDOM(listView,latitude,longitude):DOMNULL
        if(runStauts===3) VIEWDOM=(
            <div className="main_received">
                <div className="received_text">已收班</div>
                <div className="received_font">距发班还有{lineDetails.eachClassHour}小时</div>
            </div>
        )
        return VIEWDOM
    }
    _setChildrenBusDOM = (view,lat,lng) =>{
        let childredView=view.slice(0,2)
        // childredView=childredView.reverse()
        let DOM=(<Fragment>
                    {
                        childredView.map((item,index)=>{
                            return (
                                <div className="main_state" key={index}>
                                    {/* 即将到站 ：4 */}
                                    {/* 已到站  ：3 */}
                                    {/* 未过站  ：2 */}
                                    {/* 过站  ：1 */}
                                    {this._setBoxDOM(item,lat,lng)}
                                </div>
                            )
                        })
                    }
                    {
                        childredView.length>1
                        ? <div className="main_mero" onClick={()=>this._changeMero('/mero')}>
                            <img src={mero} alt="" />
                            <span>更多</span>
                        </div>
                        : null
                    }
                </Fragment>)
        return DOM
    }
    _setBoxDOM = (list,lat,lng) =>{
        const { siteOrder } = this.state
        let { pullIn,drawInTime,nextNum ,lngLat} = list
        let lngLatSlice= lngLat.split(',')
        let boxDOM
        if(pullIn === 2) boxDOM=(<Fragment>
            <div className="state_top">
                <img src={signal} alt="" />
            </div>
            {
                drawInTime<60
                ?<div className="state_mid">{drawInTime} <span>秒</span></div>
                :<div className="state_mid">{Math.ceil(drawInTime/60)} <span>分</span></div>
            }
            <div className="state_fl">{(siteOrder-nextNum)+1} 站 / {(_getDistance(lat,lng,lngLatSlice[1],lngLatSlice[0])/1000).toFixed(1)} km</div>
        </Fragment>)
        if(pullIn===3) boxDOM=(<div className="state_i">已到站</div>)
        if(pullIn===4) boxDOM=(<div className="state_i">即将到站</div>)
        return boxDOM
    }
    _changeIndex(index){
        if(index===1){
            const { upDown }=this.state
            let id=upDown===2?1:2
            this.setState({
                 upDown:id
            },()=>{
                this._getSiteDetails()
            })
        }else{
            this._getSiteDetails()
        }
    }
    _SETDATA = ()=>{
        const { siteOrder,upDown } = this.state
        const { lineId } = this.props.location.state
        const { latitude ,longitude} = this.props.mylocation
        let { beginSite ,endSite,beginTime,endTime,ticketPrice,name} = this.state.lineDetails
        let meroAll={
            carNum: 0,
            lineId: lineId,
            siteOrder: siteOrder,
            siteRoad: 0,
            upDown: upDown,
            lat:latitude,
            long:longitude
        }
        let topData={beginSite ,endSite,beginTime,endTime,ticketPrice,name}
        return {
            meroAll,topData
        }
    }
    _changeMero = (page,pageIndex=false) =>{//跳转查看更多页面
        const { viewData,siteOrder,upDown } = this.state
        const { lineId }=this.props.location.state
        const { viewList } = viewData
        let MYDATA={viewList,siteOrder,lineId,upDown}
        let req=this._SETDATA()
        this.props.history.push({pathname:page,state:{
            meroAll:pageIndex?JSON.stringify(MYDATA):req.meroAll,
            topData:req.topData}})
    }
    render() {
        const { lineDetails,viewData,arriveTIME } = this.state
        const { list,runStauts,viewList,mapUrl } = viewData
        return (
            <Fragment>
                {/* ifame start */}
                {/* {this._setIfameDOM()} */}
                {/* ifame  end */}
                <div className="details">
                    <div className="details_top">
                        <div className="header_left" onClick={this._goBack}>
                            <div className="top_one headeer"></div>
                            <div className="top_left">{lineDetails.name}</div>
                        </div>
                        <img onClick={()=>this._getIfame(mapUrl)} src={ map6 } alt="" />
                    </div>
                    <Busheader bus={lineDetails}></Busheader>
                    <div className="details_banner" style={this._setBackground(runStauts)}>
                        <div className="banner_main" style={this._setBannerStyle(runStauts,list)}>
                            {this._setBusStateDOM(runStauts,list)}
                        </div>
                    </div>
                    <div className="details_box wrapper">
                        <div className="conte" style={{width:this._setWidth(viewData.viewList.length)}}>
                            {
                                viewList.map((item,index)=>{
                                    return (
                                        <div className="box_line" style={this._setStyle(item.color)} key={index}>
                                            <img className="line_jian" src={arrowsFFF} alt="" />
                                            {/* 自身定位图标 */}
                                            {this._setSelfLocation(item)}
                                            {/* 车辆图标 */}
                                            {this._setBusImgDOM(item.busLocat)}
                                            {/* 车辆名称&&距离自身最近的车站标识 */}
                                            {this._setChildredDOM(item)}
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                    <div className="details_name">
                        <div className="name_left">
                            <div className="left_one">目的地</div>
                            <div className="left_two" onClick={()=>this._changeMero('/destination',true)}>
                                <span>{arriveTIME ? arriveTIME.name : '输入公交线路、车站、地点'}</span>
                                <img src={downRight} alt="" />
                            </div>
                        </div>
                        <div className="name_right">  {arriveTIME ? arriveTIME.time : '--秒'}  到达</div>
                    </div>
                    <div className="details_fix">
                        {DETAILSFIXED.map((item)=>{
                            return (
                                <div className="fix_box" key={item.id} onClick={()=>this._changeIndex(item.id)}>
                                    <img src={item.img} alt="" />
                                    <div>{item.text}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
            </Fragment>
        )
    }
}

const mapStateToProps=(state)=>({mylocation:state.locationCity})
const mapDisPatchToProps=(dispatch)=>({
    _changerecommend:( busReal ) =>{
        dispatch({type:RECOMMENDLINE,data:busReal })
    }
})

export default connect(mapStateToProps,mapDisPatchToProps)( Details )